import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
    methods: {
        showErrorToast({
            variant = "danger",
            position = "top-right",
            title = "Error",
            icon = "XIcon",
            text = Error,
        }) {
            this.$toast(
                {
                    component: ToastificationContent,
                    props: {
                        title,
                        icon,
                        text,
                        variant,
                    },
                },
                {
                    position,
                },
            )
        },

        showGenericToast({
            variant = "success",
            position = "top-right",
            title = "Genial",
            icon = "CheckIcon",
            type = null,
            error = false,
            text = "",
        }) {
            let message = "";
            type != null ? icon = !error ? "CheckIcon" : "XIcon" : icon;


            if (type == "register") {
                message = "registr";
            } else if (type == "update") {
                message = "actualiz"
            } else if (type == "delete") {
                message = "elimin";
            }

            if (type != null) {
                text = text == "" ? !error ? "Se " + message + " correctamente" : "No se " + message + ", intentelo nuevamente por favor" : text
            }

            this.$toast(
                {
                    component: ToastificationContent,
                    props: {
                        title,
                        icon,
                        text,
                        variant,
                    },
                },
                {
                    position,
                },
            )
        },
        showQuestionSwal(buttonText) {
            return this.$swal({
                title: `Estas seguro de ${buttonText}?`,
                text: 'No se podr revertir esto!',
                icon: 'warning',
                showCancelButton: true,
                buttonsStyling: false,
                confirmButtonText: `Si, ${buttonText}!`,
                customClass: {
                    confirmButton: 'btn btn-warning mr-1',
                    cancelButton: 'btn btn-danger  ',
                },
            })
        },
        showSuccessSwal() { },

        showGenericConfirmSwal({
            title = "Ests seguro?",
            text = "No podrs revertir esto!",
        }) {
            return this.$swal({
                icon: "question",
                title,
                text,
                imageWidth: 70,
                showCancelButton: true,
                buttonsStyling: false,
                confirmButtonText: "Confirmar",
                cancelButtonText: "Cancelar",
                customClass: {
                    confirmButton: "btn btn-primary mr-1",
                    cancelButton: "btn btn-outline-danger  ",
                },
            });
        },

        showSuccessSwal() {
            this.$swal({
                icon: "success",
                title: "Operacin exitosa!",
                showConfirmButton: false,
                timer: 1500,
                customClass: {
                    confirmButton: 'btn btn-primary',
                },
                buttonsStyling: false,
            })
        },

        showErrorSwal() {
            this.$swal({
                title: 'Ups...!!',
                text: 'Al parecer algo sali mal!, Intenta de nuevo por favor.',
                icon: 'error',
                customClass: {
                    confirmButton: 'btn btn-danger',
                },
                buttonsStyling: false,
            })
        },

        addPreloader() {
            this.$store.commit('app/SET_LOADING', true)
        },

        removePreloader() {
            this.$store.commit("app/SET_LOADING", false);
        },
    }
}